
.container-home {
  position: relative;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.group-date {
  font-weight: bold;
  margin-bottom: 10px;
}

.task-tab {
  border: 1px solid #ccc;
  margin-bottom: 5px;
  cursor: pointer;
  overflow: hidden;
  max-height: 50px;
  transition: max-height 0.5s ease-in-out, box-shadow 0.05s ease-in-out; 
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.task-tab:hover {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
}

.task-tab.expanded {
  max-height: 500px;
}

.tab-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  background-color: #f0f0f0;
  font-weight: bold;
}

.tab-details {
  padding: 8px;
  display: none;
}

.task-tab.expanded .tab-details {
  display: block;
}

.task-tabs {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  background-color: #f9f9f9;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0px; 
}

.prev-button.hidden,
.next-button.hidden {
  visibility: hidden;
  width: 0;
}

.prev-button.visible,
.next-button.visible {
  visibility: visible;
  width: auto;
}
.pagination button,
.pagination span {
  margin: 0 10px;
}

.button-container {
  display: flex;
  gap: 10px;
}

.task-item {
  margin-bottom: 25px;
}

.head {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-right {
  display: flex;
  align-items: center;
}

.sort-options {
  margin-left: 20px;
}

.container {
  position: relative;
}

.create-task-button {
  position: absolute;
  bottom: 20px;
  right: 20px;
}
