
.layout {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #333;
    color: white;
  }
  
  .headerContent {
    display: flex;
    align-items: center;
  }

  .headerTitle {
    flex-grow: 1;
    text-align: center;
  }
  
  .heading {
    margin: 0;
  }
  
  .userSection {
    display: flex;
    align-items: center;
  }
  

.username {
  margin-right: 20px;
  margin-top: 10px;
}

.sidebarButton {
    position: absolute;
    top: 20px;
    right: -50px; 
    background-color: #333;
    color: white;
    border: none;
    padding: 10px;
    cursor: pointer;
    z-index: 999;
    transition: right 0.3s ease; 
    margin-top: 10px;
  }
  
  .sidebar {
    position: fixed;
    top: 0;
    left: -250px; 
    width: 250px;
    height: 100%;
    background-color: #333;
    transition: left 0.3s ease;
    z-index: 998;
  }
  
  .sidebar.open {
    left: 0;
  }
  
  .sidebar.open .sidebarButton {
    right: 20px; 
  }
  .layout.sidebarOpen .mainContent {
    transform: translateX(250px); 
    transition: transform 0.3s ease;
  }

  .mainContent {
    flex-grow: 1;
    transition: transform 0.3s ease; 
  }

  .sidebar h2{
    text-align: center;
    color: #fff;
  }

  .sidebar ul {
    margin-top: 100px;
    list-style: none;
    padding: 0;
  }
  
  .sidebar ul li {
    margin-bottom: 10px;
  }
  
  .sidebar ul li a {
    display: block;
    padding: 10px;
    text-decoration: none;
    color: #fff;
    background-color: #444;
    transition: background-color 0.3s ease;
  }
  
  .sidebar ul li a:hover {
    background-color: #ddd;
  }
  