.dashboard-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto auto;
    gap: 10px;
    padding: 10px;
    height: 90vh;
  }
  
  .user-list-container {
    grid-column: 1 / span 1;
    grid-row: 1 / span 1;
    min-width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    max-height: 600px;
    overflow: auto
  }
  
  .team-table-container {
    grid-column: 1 / span 1;
    grid-row: 2 / span 1;
    min-width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    max-height: 600px;
    overflow: auto
  }
  
  .calendar-container {
    grid-column: 2 / span 1;
    grid-row: 1 / span 1;
    min-width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  
  .user-marks-container {
    grid-column: 2 / span 1;
    grid-row: 2 / span 1;
    min-width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    max-height: 600px;
    overflow: auto;
  }
  
  .calendar-entry-container {
    grid-column: 3 / span 1;
    grid-row: 1 / span 2;
    min-width: 250px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    max-height: 825px;
    overflow: auto;
  }
  