.calendar-entry {
    margin: 10px;
  }
  
  .input-row {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
    margin-top: 0;
    gap: 10px;
  }

  h4 {
    margin-bottom: 4px;
  }
  ul {
    padding-inline-start: 20px;
    margin: 0;
  } 
  
  .input-row select {
    margin-bottom: 10px;
  }

  .input-row input {
    width: 100px;
    padding: 0px;
    margin-bottom: 10px;
  }

  .input-row label {
    display: inline-block;
    margin-bottom: 10px;
  }
  
  .description-row {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }

  .description-row textarea {
    width: calc(70% - 10px);
    box-sizing: border-box;
    margin-left: 10px;
  }

  .slider-checkbox {
    display: none;
  }
  
  .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .header-container h2{
    margin-top: 0;
  }

  .toggle-container {
    display: flex;
    align-items: center;
  }

  .toggle-button {
    width: 100px;
    height: 30px;
    border: 1px solid #ccc;
    border-radius: 10px;
    position: relative;
    display: inline-block;
    cursor: pointer;
    overflow: hidden;
    background-color: #ccc;
    margin-bottom: 0px;
  }
  
  .slider {
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background-color: #fff;
    transition: transform 0.3s ease-in-out;
  }

  
  .text-container {
    position: relative;
    top: 0;
    left: 0;
    width: 200%;
    height: 100%;
    display: flex;
    justify-content: center;
    transition: transform 0.3s ease-in-out;
  }
  
  .text {
    position: relative;
    z-index: 1;
    font-size: 12px;
    align-self: center;
    transition: opacity 0.3s;
    pointer-events: none;
  }
  
  .slider-checkbox:checked + .toggle-button .text.generate {
    opacity: 0;
    transform: translateX(0%);
  }
  
  .slider-checkbox:not(:checked) + .toggle-button .text.entry {
    opacity: 0;
    transform: translateX(0%);
  }
  
  .slider-checkbox:checked + .toggle-button .text-container {
    transform: translateX(-7%);
  }

  .slider-checkbox:not(:checked) + .toggle-button .text-container {
    transform: translateX(-45%);
  }

  .slider-checkbox:checked + .toggle-button .slider {
    transform: translateX(100%);
  }

  .delete-button {
    padding: 6px 12px;
    background-color: transparent;
    color: black;
    text-decoration: underline;
    border: none;
    border-radius: 4px;
    margin-left: 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
