.teamTable {
    font-family: Arial, sans-serif;    
    margin: 10px;
  }
  
  .teamTable h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  .teamTable table {
    width: 100%;
    border-collapse: collapse;
    margin: auto;
    text-align: left;
  }
  .teamTable th,
  .teamTable td {
    padding: 8px 12px;
    border: 1px solid #ccc;
    text-align: center;
  }
  
  .teamTable th {
    background-color: #f2f2f2;
  }
  
  .teamTable tbody tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .teamTable button {
    margin-right: 5px;
    cursor: pointer;
    border: none;
    border-radius: 3px;
    background-color: #405d7c;
    color: #fff;
    font-size: 0.9rem;
  }
  
  .teamTable button:hover {
    background-color: #405d7c;
  }
  
  .teamHeader {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .teamHeader span {
    grid-column: span 2;
  }

  .actions {
    display: flex;
    gap: 10px; 
    align-items: center;
  }

  .actions button {
    padding: 0px;
    background-color: transparent;
    color: #000;
    text-decoration: underline;
    margin: 0;
  }
  .actions button:hover {
    background-color: transparent;
  }