.user-list {
  font-family: Arial, sans-serif;
  max-width: 600px;
  margin: 10px;
}

.user-item {
  border: 1px solid #ccc;
}

.user-tab {
  cursor: pointer;
  padding: 2px;
  background-color: #f7f7f7;
  border-bottom: 1px solid #ccc;
  padding-bottom: 4px;
}

.user-tab:hover {
  background-color: #eaeaea;
}

.expanded {
  background-color: #fff;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.tab-header {
  font-weight: bold;
  margin-bottom: 5px;
}

.tab-details {
  padding: 10px;
}

.tab-details input[type='text'] {
  width: 100%;
  padding: 5px;
  margin-bottom: 5px;
}

.button-container {
  margin-top: 10px;
}

.button-container button {
  margin-right: 5px;
  padding: 8px 15px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
}

.button-container button:hover {
  background-color: #ddd;
}
